body {
  margin: 0;
  line-height: normal;
}

:root {
  /* fonts */
  --font-inter: Inter;

  /* font sizes */
  --font-size-mini-4: 14.4px;
  --font-size-2xs-8: 10.8px;
  --font-size-xs-7: 11.7px;
  --font-size-mid-5: 17.5px;

  /* Colors */
  --color-white: #fff;
  --color-black: #000;
  --color-gray: #787878;
  --color-limegreen: #00b634;
  --color-darkslategray-100: #3e3e3e;
  --color-darkslategray-200: rgba(62, 62, 62, 0.49);
  --color-gold: #ffc107;
  --color-darkgray: rgba(169, 169, 169, 0.8);

  /* Gaps */
  --gap-7xs-4: 5.4px;
  --gap-xl: 20px;
  --gap-2xs: 11px;
  --gap-10xl-1: 29.1px;
  --gap-2xs-3: 10.3px;
  --gap-2xs-4: 10.4px;
  --gap-12xs-1: 0.1px;
  --gap-9xs-2: 3.2px;
  --gap-9xs-7: 3.7px;
  --gap-9xs-3: 3.3px;
  --gap-9xs-1: 3.1px;
  --gap-10xs-3: 2.3px;
  --gap-7xs-5: 5.5px;
  --gap-7xs-3: 5.3px;

  /* Paddings */
  --padding-4xl: 23px;
  --padding-4xs: 9px;
  --padding-12xs: 1px;
  --padding-35xl: 54px;
  --padding-2xs-8: 10.8px;
  --padding-3xl: 22px;
  --padding-13xl-5: 32.5px;
  --padding-7xs: 6px;
  --padding-8xl-9: 27.9px;
  --padding-8xs: 5px;
  --padding-smi-6: 12.6px;
  --padding-6xl: 25px;
  --padding-8xs-9: 4.9px;
  --padding-xl: 20px;
  --padding-11xs-8: 1.8px;
  --padding-5xs-2: 7.2px;

  /* Border radiuses */
  --br-4xs: 9px;
  --br-3xl-1: 22.1px;
}
